@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho+B1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shippori+Mincho+B1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}body{background-color:#2a2a2a}.header{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;color:white;font-family:"Shippori Mincho B1", serif}.search{min-height:10vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;border-bottom:1px solid rgba(255,255,255,0.3)}.search input,.search button{padding:0.5rem 2rem;margin-left:10px}.search ::-webkit-input-placeholder{text-align:center;font-weight:600;font-family:"Shippori Mincho B1", serif}.search :-ms-input-placeholder{text-align:center;font-weight:600;font-family:"Shippori Mincho B1", serif}.search ::placeholder{text-align:center;font-weight:600;font-family:"Shippori Mincho B1", serif}.search-button{box-shadow:0px 1px 0px 0px #4d1142;background:linear-gradient(to bottom, #b33a9b 5%, #70155f 100%);background-color:#b33a9b;border-radius:5px;display:inline-block;cursor:pointer;color:#ffffff;font-family:"Shippori Mincho B1", serif;font-size:15px;padding:6px 36px;text-decoration:none;text-shadow:0px 1px 0px #a834a4}.search-button:hover{background:linear-gradient(to bottom, #70155f 5%, #b33a9b 100%);background-color:#70155f}.weather-wrapper{margin-top:20px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center}.error{font-size:80px;color:white}

*{margin:0;padding:0;box-sizing:border-box}.weather-card{margin:20px 5px;border-radius:20px;position:relative;overflow:hidden;width:270px;height:270px;background-color:#fff;box-shadow:0px 0px 25px 1px rgba(50,50,50,0.1);-webkit-animation:appear 500ms ease-out forwards;animation:appear 500ms ease-out forwards}.weather-card h3{position:absolute;font-family:"Shippori Mincho B1", sans-serif;font-weight:300;font-size:20px;color:#b8b8b8;bottom:40px;left:35px;opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px);-webkit-animation:title-appear 500ms ease-out 500ms forwards;animation:title-appear 500ms ease-out 500ms forwards}.weather-card p{position:absolute;font-family:"Shippori Mincho B1", sans-serif;font-weight:300;font-size:28px;color:#2a2a2a;bottom:5px;left:35px;-webkit-animation:title-appear 1s ease-out 500ms forwards;animation:title-appear 1s ease-out 500ms forwards}.weather-card span{position:absolute;font-family:"Shippori Mincho B1", sans-serif;font-weight:300;font-size:20px;color:#bbb;top:5px;left:35px;-webkit-animation:title-appear 1s ease-out 500ms forwards;animation:title-appear 1s ease-out 500ms forwards}.weather-icon{position:relative;width:120px;height:120px;top:65px;left:65px;-webkit-animation:weather-icon-move 5s ease-in-out infinite;animation:weather-icon-move 5s ease-in-out infinite}.sun{background:#ffcd41;border-radius:50%;box-shadow:rgba(255,255,0,0.1) 0 0 0 4px;-webkit-animation:light 800ms ease-in-out infinite alternate, weather-icon-move 5s ease-in-out infinite;animation:light 800ms ease-in-out infinite alternate, weather-icon-move 5s ease-in-out infinite}@-webkit-keyframes light{from{box-shadow:rgba(255,255,0,0.2) 0 0 0 10px}to{box-shadow:rgba(255,255,0,0.2) 0 0 0 17px}}@keyframes light{from{box-shadow:rgba(255,255,0,0.2) 0 0 0 10px}to{box-shadow:rgba(255,255,0,0.2) 0 0 0 17px}}.cloud{margin-right:60px;background:#b6cede;border-radius:20px;width:25px;height:25px;box-shadow:#b6cede 24px -6px 0 2px,#b6cede 10px 5px 0 5px,#b6cede 30px 5px 0 2px,#b6cede 11px -8px 0 -3px,#b6cede 25px 11px 0 -1px}.cloud:after{content:"";position:absolute;border-radius:10px;background-color:transparent;width:4px;height:12px;left:0;top:31px;-webkit-transform:rotate(30deg);transform:rotate(30deg);-webkit-animation:rain 800ms ease-in-out infinite alternate;animation:rain 800ms ease-in-out infinite alternate}@-webkit-keyframes rain{from{box-shadow:#2092a9 8px 0px,#2092a9 32px -6px,#2092a9 20px 0px}to{box-shadow:#2092a9 8px 6px,#2092a9 32px 0px,#2092a9 20px 6px}}@keyframes rain{from{box-shadow:#2092a9 8px 0px,#2092a9 32px -6px,#2092a9 20px 0px}to{box-shadow:#2092a9 8px 6px,#2092a9 32px 0px,#2092a9 20px 6px}}@-webkit-keyframes weather-icon-move{50%{-webkit-transform:translateY(-8px);transform:translateY(-8px)}}@keyframes weather-icon-move{50%{-webkit-transform:translateY(-8px);transform:translateY(-8px)}}.inspiration{margin-top:80px;color:#709fbe;font-family:"Shippori Mincho B1", sans-serif;font-weight:300;font-size:24px;text-align:center}.inspiration a{color:#ff8f8f;font-weight:400;-webkit-animation:all 300ms ease-in-out;animation:all 300ms ease-in-out}@-webkit-keyframes appear{0%{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1.05);transform:scale(1.05)}75%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes appear{0%{-webkit-transform:scale(0);transform:scale(0)}50%{-webkit-transform:scale(1.05);transform:scale(1.05)}75%{-webkit-transform:scale(0.95);transform:scale(0.95)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes title-appear{from{opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}@keyframes title-appear{from{opacity:0;-webkit-transform:translateX(150px);transform:translateX(150px)}to{opacity:1;-webkit-transform:translateX(0px);transform:translateX(0px)}}

